.header {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 70px;
    padding-right: 70px;
    box-shadow: 0 0 2px 0 rgba(29, 33, 38, 0.1), 0 0 40px 0 rgba(29, 33, 38, 0.13);
}

.header h1 {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 24px;
    text-align: end;
}